<template>
<div class="login-bg">
  <div class="login">
    <div class="login-top">登 录</div>
    <el-row>
      <el-col :span="24">
        <el-form :rules="rules" ref="loginForm" :model="loginData">
          <el-form-item prop="node">
            <el-select v-model="loginData.node" placeholder="请选择所在单位" style="width:100%">
                <el-option
                  v-for="item in nodeList"
                  :key="item.node"
                  :label="item.name"
                  :value="item.node">
                </el-option>
              </el-select>
          </el-form-item>
          <img class="hospital-icon" src="../../assets/imgs/hospital.png" alt="">
          <el-form-item prop="username">
            <el-input prefix-icon="el-icon-zhzy-user" @keydown.enter.native="handelLogin" auto-complete="on"
             v-model="loginData.username" placeholder="请输入账号" name="username"></el-input>
          </el-form-item>
          <el-form-item prop="pwd">
            <el-input prefix-icon="el-icon-zhzy-lock" v-model="loginData.pwd"
             @keydown.enter.native="handelLogin" name="password"
            type="password" placeholder="请输入密码"></el-input>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col align="center"  style="margin-top: 20px">
        <el-button type="primary" @click="handelLogin">登 录</el-button>
      </el-col>
    </el-row>
  </div>
</div>
</template>
<script>
import md5 from 'js-md5'
import {mapMutations} from 'vuex'
import {login,getAllHospital} from '../../api'
export default {
  data(){
    return{
      loginData:{
        username: '',
        pwd: '',
        node: '',
        force: '',
      },
      rules:{
        username:[{required: true, message: '账号不能为空!', trigger: 'blur'}],
        pwd:[{required: true, message: '密码不能为空!', trigger: 'blur'}]
      },
      nodeList: [],
    }
  },
  methods:{
    ...mapMutations('user',['saveUserData','getUserMap']),
    handelLogin(){
      this.$refs['loginForm'].validate((valid) => {
      if (valid) {
        this.loginData.password = md5(this.loginData.pwd)
        login(this.loginData).then(res=>{
          if(res.data.code == '0'){
            sessionStorage.setItem('token',res.data.data.token)
            localStorage.setItem('localName', this.loginData.username)
            let user = res.data.data.user
            this.saveUserData({
              uuid: user.uuid,
              node: user.node,
              username: user.username,
              usercode: user.usercode,
              deptcode: user.deptcode,
              sex: user.sex,
              pId:user.pId
            })
            // 获取权限
            let uaCodes = []
            user.userRoles.forEach(ele=>{
              uaCodes.push(ele.code)
            })
            sessionStorage.setItem('uaCodes',uaCodes.join(','))
            localStorage.setItem('localNode', this.loginData.node)
            this.getUserMap()
            this.$router.push('/')
          }else{
            if(res.data.code =='3'){ // 不同ip登录
              this.funReLoginCheck()
            }else{
              this.loginData.pwd = ''
              this.$message.error(res.data.msg)
            }
          }
        })
      } else {
        return false;
      }
    })
    },
    funReLoginCheck(){ // 强行登陆提示操作
      this.$confirm('用户已在其他IP设备登录, 是否继续登录?', '登录提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.loginData.force = "true"
          this.handelLogin()
        })
    }
  },
  mounted(){
    sessionStorage.removeItem('is_login')
    getAllHospital().then(res=>{
      if(res.data.code == '0'){
        this.nodeList = res.data.data
        this.loginData.node = localStorage.getItem('localNode')?localStorage.getItem('localNode'):''
      }else{
        this.$message.error('获取医疗单位列表错误!')
      }
    })
    if (localStorage.getItem('localName')) {
      this.loginData.username = localStorage.getItem('localName')
    }
  }
}
</script>
<style scoped>
  .login-bg{
    height: 100vh;
    background-image: url('../../assets/imgs/sign_in_back.png');
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
  }
  .login-top{
    margin-bottom: 40px;
    color: #BE8F61;
    font-size: 16px;
    text-align: center;
  }
  .login{
    position: relative;
    top: 36vh;
    margin: 0 auto;
    padding: 60px 80px;
    width: 440px;
    background: #F8EAD5;
    border-radius: 10px;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  }
  .hospital-icon{
    position: absolute;
    width: 14px;
    top: 12px;
    left: 11px;
  }
</style>




